import { _decorator, Component, Node, Collider2D, Contact2DType, PhysicsSystem2D, EPhysics2DDrawFlags, IPhysics2DContact, instantiate, resources, Prefab, Layers, Vec3, NodePool, Vec2, ProgressBar, find } from 'cc';
import { BulletPerManeger } from './BulletPerManeger';
import { combatConfigManeger } from './combatConfigManeger';
import { dijiTargetManeger } from './dijiTargetManeger';
import { livingSystemManeger } from './livingSystemManeger';
const { ccclass, property } = _decorator;

@ccclass('diji')
export class diji extends Component {
    public bulletArray = [];
    public hasEmeny:boolean = true;
    private targetArray:Array<Vec3> = null
    public bulletPool:NodePool;
    @property(livingSystemManeger)
    DijiLivSysMag:livingSystemManeger = null;
    
    public forwardSpeed:number;
    start() {
        this.initialDiji();
        let that = this;
        this.bulletPool = new NodePool();
        this.schedule(function(){
            this.hasEmeny = this.node.parent.getComponent(dijiTargetManeger).hasEmeny;
            if(this.hasEmeny){
                this.targetArray = this.node.parent.getComponent(dijiTargetManeger).emenyArray;
                // console.log('target number is ',this.targetArray.length)
                console.log('createBullet')
                that.createBullet();
                // if(that.bulletPool.size()>5){
                //     that.bulletPool.clear()
                // }
            }
        },1)

        console.log('emeny information')
        let collider = this.node.getComponent(Collider2D);
        // console.log(collider);
        // console.log(collider);
        if (collider) {
            collider.on(Contact2DType.BEGIN_CONTACT, this.onBeginContact, this);
            collider.on(Contact2DType.END_CONTACT, this.onEndContact, this);
            collider.on(Contact2DType.PRE_SOLVE, this.onPreSolve, this);
            collider.on(Contact2DType.POST_SOLVE, this.onPostSolve, this);
        }
    }
    onLoad(){
        // director.enable = true;
        //绘制物理信息
        PhysicsSystem2D.instance.enable = true;
        PhysicsSystem2D.instance.debugDrawFlags = EPhysics2DDrawFlags.Aabb | 
            EPhysics2DDrawFlags.Pair | 
            EPhysics2DDrawFlags.CenterOfMass |
            EPhysics2DDrawFlags.Joint |
            EPhysics2DDrawFlags.Shape ;
    }

    update(deltaTime: number) {
        let node =this.node.parent;
        let y = node.position.y;
        let x = node.position.x;
        if(x > 600){
            //敌军飞机不超过神木；
        }else{
            x = x + this.forwardSpeed * deltaTime;
            // y = y - 10 * deltaTime;
            node.setPosition(x,y,0);
        }
        

        for(let i=0;i<this.bulletArray.length;i++){
            let bulletNode = this.bulletArray[i];
            let istarget = bulletNode.getComponent(BulletPerManeger).istarget;
            let isOverScreen = bulletNode.getComponent(BulletPerManeger).isOverScreen;
            // console.log("是否命中",istarget)
            if(istarget || isOverScreen){
                // bulletNode.destroy()
                this.bulletPool.put(bulletNode);
            }
            
            let y = bulletNode.position.y;
            let x = bulletNode.position.x;

            if(this.targetArray.length>0){
                x = x + this.DijiLivSysMag.attackSpeed * deltaTime;
            }
            else{
                if(i%3 == 1){
                    x = x + this.DijiLivSysMag.attackSpeed * Math.cos(60 *Math.PI / 180 ) * deltaTime;
                    y = y - this.DijiLivSysMag.attackSpeed * Math.sin(60 *Math.PI / 180 ) * deltaTime;
                }else if (i%3 == 2){
                    x = x + this.DijiLivSysMag.attackSpeed * Math.cos(70 *Math.PI / 180 ) * deltaTime;
                    y = y - this.DijiLivSysMag.attackSpeed * Math.sin(70 *Math.PI / 180 ) * deltaTime;
                }else{
                    x = x + this.DijiLivSysMag.attackSpeed * Math.cos(80 *Math.PI / 180 ) * deltaTime;
                    y = y - this.DijiLivSysMag.attackSpeed * Math.sin(80 *Math.PI / 180 ) * deltaTime;
                }
            }
                
            bulletNode.setPosition(x,y,0)
            // }
            
            // TODO:出屏幕后销毁
        }
    }

    initialDiji(){
        let AttackSpeed = find("Canvas/GlobalRoundControl").getComponent(combatConfigManeger).EmenyNormal.AttackSpeed;
        let LivingValue = find("Canvas/GlobalRoundControl").getComponent(combatConfigManeger).EmenyNormal.LivingValue;
        let DefendValue = find("Canvas/GlobalRoundControl").getComponent(combatConfigManeger).EmenyNormal.DefendValue;
        let HarmValue = find("Canvas/GlobalRoundControl").getComponent(combatConfigManeger).EmenyNormal.HarmValue;
        this.DijiLivSysMag = new livingSystemManeger(LivingValue,HarmValue,DefendValue,11,AttackSpeed);
        this.forwardSpeed = find("Canvas/GlobalRoundControl").getComponent(combatConfigManeger).EmenyNormal.forwardSpeed;
        console.log("diji初始化成功")
    }

    onBeginContact (selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null) {
        // 只在两个碰撞体开始接触时被调用一次
        // this.hasEmeny = true;
        // this.targetArray.push(otherCollider.node.position)
        // console.log("maybe a information about position",otherCollider.node.position)
        // console.log('I am emeny onBeginContact');
        this.underFire(otherCollider);

    }
    onEndContact (selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null) {
        // 只在两个碰撞体结束接触时被调用一次
        // console.log('Work onEndContact');

    }
    onPreSolve (selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null) {
        // 每次将要处理碰撞体接触逻辑时被调用
        // console.log('Work onPreSolve');
    }
    onPostSolve (selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null) {
        // 每次处理完碰撞体接触逻辑时被调用
        // console.log('Work onPostSolve');

    }
    getAngle(start:Vec3,end:Vec3):number{
        let dx = start.x - (end.x );
        let dy = start.y - (end.y );
        let dir = new Vec2(dx,dy);

        let angle = dir.signAngle(new Vec2(1,0));

        let degreen = angle / Math.PI * 180;

        return -degreen
    }
    underFire(Collider: Collider2D){
        //判断是被谁攻击的

        //目前为默认减少10;
        if(Collider.node.name == "PaoBulletPer"){
            if(this.DijiLivSysMag.livingValue - 40 <= 0){
                this.node.parent.destroy()
            }else{
                this.DijiLivSysMag.livingValue -=40
                // console.log("生命值为：",this.SanShePaoLivSysMag.livingValue)
                this.node.parent.getChildByName("ProgressBar").getComponent(ProgressBar).progress = 0.5 * (this.DijiLivSysMag.livingValue/100)
            }
        }

        if(Collider.node.name == "GuangZiBulletPer"){
            if(this.DijiLivSysMag.livingValue - 80 <= 0){
                this.node.parent.destroy()
            }else{
                this.DijiLivSysMag.livingValue -=80
                // console.log("生命值为：",this.SanShePaoLivSysMag.livingValue)
                this.node.parent.getChildByName("ProgressBar").getComponent(ProgressBar).progress = 0.5 * (this.DijiLivSysMag.livingValue/100)
            }
        }

        if(Collider.node.name == "FengyiBulletPer"){
            if(this.DijiLivSysMag.livingValue - 30 <= 0){
                this.node.parent.destroy()
            }else{
                this.DijiLivSysMag.livingValue -=30
                // console.log("生命值为：",this.SanShePaoLivSysMag.livingValue)
                this.node.parent.getChildByName("ProgressBar").getComponent(ProgressBar).progress = 0.5 * (this.DijiLivSysMag.livingValue/100)
            }
        }

        
    }
    createBullet(){
        // let node = new Node();
        // node.addComponent(Sprite);
        let that = this;
        resources.load("perfabs/Bullet_Pre",Prefab,(err,Prefab)=>{
            if(!Prefab){
                // console.log('子弹预制体为空')
            }else{
                // console.log('预制体制作完成')
                let node = instantiate(Prefab);
                if(!node){
                    // console.log('没有节点')
                }else{
                    // console.log(node)
                    // let pos = new Vec2();
                    node.layer = Layers.Enum.UI_2D;
                    let x = that.node.position.x + 400;
                    let y = that.node.position.x + 225;
                    
                    // let shit = pos.set(x,y)
                    // x = shit.x - 800;                   
                    // y = shit.y - 450;
                    // console.log(x,y)
                    node.setPosition(0,0,0);
                    that.node.addChild(node);
                    that.bulletArray.push(node);
                }
                
            }
        })
    }
}


